import React from "react";
import { MDBCol, MDBContainer, MDBRow, MDBFooter } from "mdbreact";

import "./Footer.css";

const Footer = () => {
  return (
    <MDBFooter color="mdb-color" className="font-small lighten-3 pt-4 mt-4">
      <MDBContainer className="text-center text-md-left">
        <MDBRow className="my-4">
          <MDBCol md="4" lg="4">
            <h5 className="text-uppercase mb-4 font-weight-bold">
              Delivery Terms
            </h5>
            <p>
              If your order is registered in our system with the status
              “delivered” and you have not received a message, please forthwith
              email us about this fact on the following email address:
              INFO@TJ-Shop.GE and after detecting the problem, the Company shall
              take care of elimination of any misunderstanding / omission in
              timely manner.
            </p>
          </MDBCol>
          <hr className="clearfix w-100 d-md-none" />
          <MDBCol md="2" lg="2" className="ml-auto">
            <h5 className="text-uppercase mb-4 font-weight-bold">Products</h5>
            <ul className="list-unstyled">
              <p>
                <a href="/airConditioner">Air Conditioner</a>
              </p>
              <p>
                <a href="/television">Television</a>
              </p>
              <p>
                <a href="/washingMachine">Washing Machine</a>
              </p>
              <p>
                <a href="/refrigerator">Refrigerator</a>
              </p>
            </ul>
          </MDBCol>
          <hr className="clearfix w-100 d-md-none" />
          <MDBCol md="5" lg="3">
            <h5 className="text-uppercase mb-4 font-weight-bold">Address</h5>
            <p>
              <i className="fa fa-home mr-3" /> Tbilisi, Georgia
            </p>
            <p>
              <i className="fa fa-envelope mr-3" />{" "}
              Jmukhadze.Tornike@hotmail.com
            </p>
            <p>
              <i className="fa fa-phone mr-3" /> + 995 598 17 22 99
            </p>
            <p>
              <i className="fa fa-print mr-3" /> + 995 598 17 22 99
            </p>
          </MDBCol>
          <hr className="clearfix w-100 d-md-none" />
          <MDBCol md="2" lg="2" className="text-center">
            <h5 className="text-uppercase mb-4 font-weight-bold">Follow us</h5>
            <div className="mt-2">
              <a
                type="button"
                className="btn-floating btn-extra btn-fb row mt-3 "
              >
                <i className="fab fa-facebook-f" />
              </a>
              <a
                type="button"
                className="btn-floating btn-extra btn-tw row mt-3 "
              >
                <i className="fab fa-twitter" />
              </a>
              <a
                type="button"
                className="btn-floating btn-extra btn-gplus row mt-3 "
              >
                <i className="fab fa-google-plus" />
              </a>
              <a
                type="button"
                className="btn-floating btn-extra btn-dribbble row mt-3 "
              >
                <i className="fab fa-dribbble" />
              </a>
            </div>
          </MDBCol>
          <hr className="clearfix w-100 d-md-none" />
        </MDBRow>
      </MDBContainer>
      <div className="footer-copyright text-center py-3">
        <MDBContainer fluid>
          &copy; {new Date().getFullYear()} Copyright:{" "}
          <a href="https://www.MDBootstrap.com">TJ-Shop </a>
        </MDBContainer>
      </div>
    </MDBFooter>
  );
};

export default Footer;
