import React, { useState, useEffect } from "react";
import { MDBContainer, } from "mdbreact";

import "./index.css";

import NavBar from "./components/NavBar/NavBar"
import Carousel from "./components/Carousel/Carousel"
import LittleNav from "./components/LittleNav/LittleNav"
import Footer from "./components/Footer/Footer"
import Contact from "./components/InputForms/Contact/Contact"
import Login from "./components/InputForms/Login/Login"
import Register from "./components/InputForms/Register/Register"
import AddCart from "./components/Cart/AddCart"
import NotFound from "./pages/NotFound"



import Home from "./pages/Home"
import AirConditioner from "./pages/AirConditioner"
import Television from "./pages/Television"
import WashingMachine from "./pages/WashingMachine"
import Refrigerator from "./pages/Refrigerator"
import { Route, Redirect, BrowserRouter as Router, Switch } from "react-router-dom";
import { CartProvider } from "./context/CartContext";
// import PrList from "./components/InputForms/Search/PrList"
export default function App() {


  return (
    <CartProvider>
      <Router>
        <NavBar />
        <MDBContainer>
          <Carousel />
          <LittleNav />
          <Switch>
            <Route exact path="/home" component={Home} />
            <Route exact path="/airConditioner" component={AirConditioner} />
            <Route exact path="/television" component={Television} />
            <Route exact path="/washingMachine" component={WashingMachine} />
            <Route exact path="/refrigerator" component={Refrigerator} />
            <Route exact path="/addCart" component={AddCart} />
            <Route exact path="/Contact" component={Contact} />
            <Route exact path="/Login" component={Login} />
            <Route exact path="/Register" component={Register} />

            <Route exact path="/404" component={NotFound} />


            <Redirect to="/404" />
          </Switch>

        </MDBContainer>
        <Footer />
      </Router>
    </CartProvider>
  );

}


