import React, { useContext, Component } from "react";
import {
  MDBBtn,
  MDBCard,
  MDBCardBody,
  MDBCardImage,
  MDBCardTitle,
  MDBCardText,
  MDBCol,
  MDBRow,
  MDBNavLink,
  MDBModal,
  MDBModalBody,
  MDBModalHeader,
  MDBModalFooter,
  MDBContainer,
  MDBIcon
} from "mdbreact";
import { CartContext } from "../context/CartContext";
import homeProduct from "../Products/homeProduct";

import "./Pages.css";

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      products: homeProduct,
      cart: [],
      totalBill: 0
    };
  }

  addtoCart = id => {
    const temp = [...this.state.products];
    // const cart = [...this.state.cart];
    const index = temp.findIndex(x => x.id === id);
    const product = temp[index];
    product.quantity = 1;
    product.total = product.Price;
    const totalBill = this.state.totalBill + product.total;
    this.setState({
      cart: [...this.state.cart, product],
      totalBill: totalBill
    });
  };
  removeCart = id => {
    const cart = [...this.state.cart];
    const res = cart.filter(x => x.id !== id);
    this.setState({
      cart: res
    });
  };
  incQuantity = id => {
    const cart = [...this.state.cart];
    const index = cart.findIndex(x => x.id === id);
    const product = cart[index];
    product.quantity = product.quantity + 1;
    product.total = product.Price * product.quantity;
    let totalBill;
    if (product.quantity === 1) {
      totalBill = this.state.totalBill;
    } else {
      totalBill = this.state.totalBill + product.total;
    }
    cart.splice(index, 1, product);
    this.setState({
      cart: cart,
      totalBill: totalBill
    });
  };
  decQuantity = id => {
    const cart = [...this.state.cart];
    const index = cart.findIndex(x => x.id === id);
    const product = cart[index];
    if (product.quantity <= 1) return;
    product.quantity = product.quantity - 1;
    product.total = product.Price * product.quantity;
    cart.splice(index, 1, product);
    this.setState({
      cart: cart
    });
  };

  state = {
    modal: false
  };

  toggle = () => {
    this.setState({
      modal: !this.state.modal
    });
  };

  render() {
    return (
      <MDBContainer>
        <MDBRow>
          {this.state.products.map((item, i) => {
            return (
              <MDBCol md="4" className="mt-4">
                <MDBCard>
                  <MDBCardImage className="img-fluid" src={item.src} waves />
                  <MDBCardBody>
                    <MDBCardTitle>{item.Name}</MDBCardTitle>
                    <MDBCardText>{item.Brand}</MDBCardText>
                    <MDBCardText>{item.Price} ₾</MDBCardText>
                    <div className="modal-btn">
                      <MDBBtn onClick={() => this.addtoCart(item.id)}>
                        Add to cart
                      </MDBBtn>
                      <MDBBtn color="info" onClick={this.toggle}>
                        buy
                      </MDBBtn>
                    </div>
                  </MDBCardBody>
                </MDBCard>
              </MDBCol>
            );
          })}

          {/* BUTTON */}

          {/* MODAL */}
          <MDBModal isOpen={this.state.modal} toggle={this.toggle}>
            <MDBModalHeader toggle={this.toggle}>Add to cart</MDBModalHeader>
            <MDBModalBody>
              <div>
                {this.state.cart.length > 0 ? (
                  <table>
                    <tbody>
                      {this.state.cart.map((item, i) => {
                        return (
                          <tr key={i}>
                            <td>
                              <img width="100px" src={item.src} alt="" />
                            </td>
                            <td>
                              <strong>{item.Name}</strong>
                            </td>
                            <td>{item.quantity}</td>

                            <td>
                              <strong>{item.total} ₾</strong>
                            </td>
                            <td>
                              <button
                                className="modal-btn-btn"
                                onClick={() => this.incQuantity(item.id)}
                              >
                                <i class="fas fa-plus-circle icons-btn-color"></i>
                              </button>
                            </td>
                            <td>
                              <button
                                className="modal-btn-btn"
                                onClick={() => this.decQuantity(item.id)}
                              >
                                <i class="fas fa-minus-circle icons-btn-color"></i>
                              </button>
                            </td>
                            <td>
                              <button
                                className="modal-btn-btn"
                                onClick={() => this.removeCart(item.id)}
                              >
                                <i class="fas fa-trash icons-btn-color"></i>
                              </button>
                            </td>
                          </tr>
                        );
                      })}
                    </tbody>
                  </table>
                ) : (
                  <h2>Cart is emty</h2>
                )}
              </div>
            </MDBModalBody>
            <MDBModalFooter>
              <MDBBtn color="secondary" onClick={this.toggle}>
                Close
              </MDBBtn>
              <MDBBtn color="primary">checkout</MDBBtn>
            </MDBModalFooter>
          </MDBModal>
        </MDBRow>
      </MDBContainer>
    );
  }
}
export default Home;
