import React, { Component } from "react";
import { MDBContainer, MDBRow, MDBCol, MDBIcon, MDBBtn, MDBInput, } from "mdbreact";
import "./Contact.css"



class Contact extends Component {
  constructor() {
    super();
    this.state = {
      name: "",
      email: "",
      message: "",
      subject: ""
    };
  }
  handleInput = e => {
    this.setState({
      [e.target.name]: e.target.value
    }

    )
  }
  sendForm = () => {
    fetch('https://jmukhadze.tornike@hotmail.com/.', {
      method: 'POST',
      body: JSON.stringify({
        name: this.state.name,
        email: this.state.email,
        message: this.state.message,
        subject: this.state.subject
      })
    })
      .then(res => res.json())
      .catch(err => alert("Message Sent"));

  }
  render() {

    return (
      <MDBContainer>
        <h2 className="h1-responsive font-weight-bold text-center my-5">
          SEND A SUPPORT REQUEST
      </h2>
        <p className="text-center w-responsive mx-auto pb-5">
          Please fill out this form and one of our support team members will be in touch with you shortly
      </p>
        <MDBRow>
          <MDBCol md="9" className="md-0 mb-5">
            <form>
              <MDBRow>
                <MDBCol md="6">
                  <div className="md-form mb-0">
                    <MDBInput
                      label="Your name"
                      group
                      type="text"
                      validate
                      error="wrong"
                      success="right"
                      name="name"
                      value={this.state.name}
                      onInput={this.handleInput} />

                  </div>
                </MDBCol>
                <MDBCol md="6">
                  <div className="md-form mb-0">
                    <MDBInput
                      label="Your email"
                      group
                      type="email"
                      validate
                      error="wrong"
                      success="right"
                      name="email"
                      value={this.state.email}
                      onInput={this.handleInput}
                    />
                  </div>
                </MDBCol>
              </MDBRow>
              <MDBRow>
                <MDBCol md="12">
                  <div className="md-form mb-0">
                    <MDBInput type="text" group success="right" name="subject" validate error="wrong" label="Subject" value={this.state.subject} onInput={this.handleInput} />
                  </div>
                </MDBCol>
              </MDBRow>
              <MDBRow>
                <MDBCol md="12">
                  <div className="md-form mb-0">
                    <MDBInput
                      type="textarea"
                      rows="2"
                      label="Your message"
                      name="message"
                      value={this.state.message}
                      onInput={this.handleInput}
                    />
                  </div>
                </MDBCol>
              </MDBRow>
            </form>
            <div className="text-center text-md-left">
              <MDBBtn color="primary" size="md" onClick={this.sendForm}>

                Send
            </MDBBtn>

            </div>
          </MDBCol>
          <MDBCol md="3" className="text-center">
            <ul className="list-unstyled mb-0">
              <li>
                <MDBIcon icon="map-marker-alt" size="2x" className="blue-text" />
                <p>Tbilisi, Georgia</p>
              </li>
              <li>
                <MDBIcon icon="phone" size="2x" className="blue-text mt-4" />
                <p>+ 995 598 17 22 99</p>
              </li>
              <li>
                <MDBIcon icon="envelope" size="2x" className="blue-text mt-4" />
                <p>Jmukhadze.Tornike@hotmail.com</p>
              </li>
            </ul>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    );

  }

}


export default Contact;