import React from "react";
import { } from "react-router-dom";
import "./Login.css"
import { MDBAlert } from "mdbreact";

class Login extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      user: {

        Email: "",
        Password: ""
      },
      submitted: false
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange = e => {
    let user = { ...this.state.user };
    let name = e.target.name;
    let defaultValue = e.target.defaultValue;
    user[name] = defaultValue;
    this.setState({ user });
  };
  handleSubmit = e => {
    alert("You Logged In")


  };

  render() {
    const { user, submitted } = this.state;
    return (
      <form
        onSubmit={this.handleSubmit}
        className="text-center border border-light p-5 mt-4"
        action="#!"
      >
        <p className="h4 mb-4">LOG IN</p>

        <p>Log in using the email and password created when you registered </p>

        <input
          name="email"
          type="email"
          defaultValue={user.Email}
          id="defaultSubscriptionFormEmail"
          className="form-control mb-4"
          placeholder="Enter your email"
          onChange={this.handleChange}
        />
        <input
          name="password"
          type="password"
          defaultValue={user.Password}
          id="defaultSubscriptionFormPassword"
          className="form-control mb-4"
          placeholder="Enter your password"
          onChange={this.handleChange}
        />

        <button className="btn btn-info btn-block" type="submit">
          Sign in
        </button>

      </form>
    );

  }




}
export default Login;
