import React, { Component } from "react";
import {
  MDBNavbar,
  MDBNavbarBrand,
  MDBNavbarNav,
  MDBNavItem,
  MDBNavbarToggler,
  MDBNavLink,
  MDBCollapse,
  MDBDropdown,
  MDBDropdownToggle,
  MDBDropdownMenu,
  MDBDropdownItem,
  MDBIcon,
  MDBInputGroup,
  MDBBtn,
  MDBFormInline
} from "mdbreact";
import {} from "react-router-dom";
import "../NavBar/NavBar.css";

const prnames = [
  "მაცივარი",
  "გაზქურა",
  "გაზი",
  "ტელევიზორი",
  "კონდინციონერი",
  "სარეცხი მანქანა",
  "Air Condintioner",
  "Television",
  "Washing Machine",
  "Refrigerator"
];

class NavBar extends Component {
  logValue = value => {
    console.log(value);
  };
  state = {
    isOpen: false
  };

  toggleCollapse = () => {
    this.setState({ isOpen: !this.state.isOpen });
  };

  render() {
    return (
      <MDBNavbar color="default-color" dark expand="md">
        <MDBNavbarBrand href="/home">
          <strong className="white-text">TJ-SHOP</strong>
        </MDBNavbarBrand>
        <MDBNavbarToggler onClick={this.toggleCollapse} />
        <MDBCollapse id="navbarCollapse3" isOpen={this.state.isOpen} navbar>
          <MDBNavbarNav left>
            <MDBNavItem>
              <MDBNavLink to="/airConditioner">AIR CONDITIONER</MDBNavLink>
            </MDBNavItem>
            <MDBNavItem>
              <MDBNavLink to="/television">TELEVISION</MDBNavLink>
            </MDBNavItem>
            <MDBNavItem>
              <MDBNavLink to="/washingMachine">WASHING MACHINE</MDBNavLink>
            </MDBNavItem>
            <MDBNavItem>
              <MDBNavLink to="/refrigerator">REFRIGERATOR</MDBNavLink>
            </MDBNavItem>
          </MDBNavbarNav>
          <MDBNavbarNav right>
            <MDBNavItem>
              <MDBNavLink className="waves-effect waves-light" to="/Contact">
                <MDBIcon icon="envelope" className="mr-1" />
                CONTACT
              </MDBNavLink>
            </MDBNavItem>
            <MDBNavItem>
              <MDBDropdown>
                <MDBDropdownToggle nav caret>
                  <MDBIcon icon="user" />
                </MDBDropdownToggle>
                <MDBDropdownMenu className="dropdown-default">
                  <MDBNavLink className="L-input" to="/Login">
                    LOGIN
                  </MDBNavLink>
                  <MDBNavLink className="L-input" to="/Register">
                    REGISTER
                  </MDBNavLink>
                </MDBDropdownMenu>
              </MDBDropdown>
            </MDBNavItem>
          </MDBNavbarNav>
          <MDBFormInline>
            <MDBInputGroup
              className="form-control form-control-sm ml-3 w-75"
              type="text"
              hint="Search"
              aria-label="Search"
              clear
              id="search-names"
              data={prnames}
            >
              <button type="button" type="submit" className="search-button">
                <i className="fas fa-search"></i>
              </button>
            </MDBInputGroup>
          </MDBFormInline>
        </MDBCollapse>
      </MDBNavbar>
    );
  }
}

export default NavBar;
